.backdrop {
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, .25);
  transition: opacity .2s ease-in;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--active {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity .2s ease-in;
}

.backdrop--active .modal {
  transform: scale(.9);
}

.modal {
  width: 280px;
  height: 100%;
  visibility: visible;
  opacity: 1;
  background-color: var(--white-color);
  border-radius: 4px;
  transition: opacity .25s ease-in, visibility .25s ease-in, transform .25s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

